import styled from "styled-components";
import theme from "Document.theme.js";

export const SearchPlus = styled.div`
	display: flex;
	.Select-placeholder,
	.Select--single > .Select-control .Select-value {
		bottom: 0;
		color: #aaa;
		left: 0;
		line-height: 34px;
		padding-left: 10px;
		padding-right: 10px;
		position: absolute;
		right: 0;
		top: 0;
		max-width: 100%;
		overflow: hidden;
		-o-text-overflow: ellipsis;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.Select.has-value.is-clearable.Select--single > .Select-control .Select-value {
		padding-right: 42px;
	}
	.Select-arrow-zone {
		cursor: pointer;
		display: table-cell;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 25px;
		padding-right: 5px;
	}
	.Select-clear-zone {
		-webkit-animation: Select-animation-fadeIn 200ms;
		animation: Select-animation-fadeIn 200ms;
		color: #999;
		cursor: pointer;
		/* display: table-cell; */
		display: none;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 17px;
	}
`;



